  .image-dialog {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image-dialog img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
  }

  .close-button {
    color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 10px;
    right: 10px;
  }
  
  .delete-button {
    position: absolute;
    bottom: 5px;
    left: 10px;
  }


