/* Hide the scrollbar track */
::-webkit-scrollbar {
  width: 5px;
}

/* Style the scrollbar thumb */
::-webkit-scrollbar-thumb {
  background-color: #c2c2c2;
}

/* Change the scrollbar thumb color on hover */
::-webkit-scrollbar-thumb:hover {
  background-color: #555555;
}

/* Hide the scrollbar track when it overlaps with rounded elements */
::-webkit-scrollbar-track:vertical {
  background: none;
  margin: 15px;
}

::-webkit-scrollbar-track:horizontal {
  background: none;
  margin: 5px;
}

/* Apply rounded corners to the scrollbar thumb */
::-webkit-scrollbar-thumb:horizontal {
  border-radius: 15px;
}

::-webkit-scrollbar-thumb:vertical {
  border-radius: 5px;
}



.form-line-active input:focus, .form-line-active textarea:focus {
  border: 1px solid green !important;
  box-shadow: 0 0 3px green !important;
  -moz-box-shadow: 0 0 3px green !important;
  -webkit-box-shadow: 0 0 3px green !important;
  }
  
  input:focus, textarea:focus, select:focus {
  outline-offset: 0px !important;
  outline: none !important;
  }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
