.button_plus {
  position: relative;
  width: 100%;
  height: 100%;
  background: #ffecb3;
  cursor: pointer;
  border: 1px solid #9e9e9e;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.button_plus_icon {
  font-size: 16px;
  font-weight: bold;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.button_plus:before,
.button_plus:after {
  content: '';
  position: absolute; /* Use position: absolute */
  transform: translate(-50%, -50%);
  background: #9e9e9e;
  border-radius: 15px;
}

.button_plus:after {
  height: 8px;
  width: 40px;
  top: 50%;
  left: 50%;
}

.button_plus:before {
  height: 40px;
  width: 8px;
  top: 50%;
  left: 50%;
}

.button_plus:hover:before,
.button_plus:hover:after {
  background: #8f8f8f;
  transition: 0.2s;
}

.button_plus:hover {
  background-color: #ffefc0;
  transition: 0.2s;
}
