.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
  }
  
  .modal-content {
    background-color: rgb(241, 241, 241);
    padding: 20px;
    border-radius: 15px;
    max-width: 300px;    
    width: 100%;
    max-height: 90vh;
    margin-top: 20px;
    margin-bottom: 20px;
    overflow-x: auto;
  }
  
  .modal-close {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 40px;
    color: #f0f0f0;
    cursor: pointer;
  }

  .input-field {
    display: block;
    width: 93%;
    padding: 5px;
    margin: 10px;
    font-size: 14px;
    font-weight: 550;
    color: #424242;
    border-color: #e7e7e7;
    border-width: 3px;
    border-radius: 10px;
  }
  